
body {
  color: slateblue;
  font-family: 'Roboto', sans-serif;
}

.form_header {
  margin-left: 8px;
}

.form_container {
  width: 260px;
  margin: 60px auto;
  padding: 20px;
}

input, textarea {
  padding: 8px 8px 8px 40px;
  margin: 8px;
  width: 100%;
  border-radius: 2px;
  border: 1px solid slateblue;

  &:focus {
    outline-color: #5DFDCB;
    border:  1px solid #5DFDCB;
  }
}

.text, .email, .textarea, .tel {
  position: relative;
  width: 100%;
  border-radius: 2px;
}

.textarea {
  height: 72px;
}

.select {
  position: relative;
  width: 100%;
  border-radius: 2px;
}

form {
}

button[type=button] {
  margin-left: 7px;
  margin-top: 20px;
  font-size: 18px;
  border: 1px solid slateblue;
  border-radius: 2px;
  background-color: white;
  color: slateblue;

  &:hover {
    background-color: slateblue;
    color: white;
  }
}

.fa {
  position: absolute;
  left: 14px;
  top: 10px;
}
